@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;500;600&display=swap');

:root
{
    --orange:#ff7b00;
    --black:#192a56;
    --light-color:#666;
    --box-shadow:0 .5rem 1.5rem rgba(0,0,0,.1);
}

*
{
    font-family: 'Nunito', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    outline: none;
    border: none;   
    text-transform: capitalize;
    transition: all .2s linear;
}

html
{
    font-size: 62.5%;
    overflow-x: hidden;
    scroll-padding-top: 5.5rem;
    scroll-behavior: smooth;
}

section
{
    padding: 2rem 9%;
}

section:nth-child(even)
{
    background: #eee;
}

.sub-deading
{
    text-align: center;
    color: var(--orange);
    font-size: 2rem;
    padding-top: 1rem;
}

.heading
{
    text-align: center;
    color: var(--black);
    font-size: 3rem;
    padding-bottom: 2rem;
    text-transform: uppercase;
}

.btn
{
    margin-top: 1rem;
    display: inline-block;
    font-size: 1.7rem;
    color: #fff;
    background: var(--black);
    border-radius: .5rem;
    cursor: pointer;
    padding: .8rem 2rem;
}

.btn:hover
{
    background: var(--orange);
    letter-spacing: .1rem;
}
.footer .box-container
{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
    gap: 1.5rem;
}

.footer .box-container .box h3
{
    padding: .5rem 0;
    font-size: 2.5rem;
    color: var(--black);
}

.footer .box-container .box a
{
    display: block;
    padding: .5rem 0;
    font-size: 1.5rem;
    color: var(--light-color);
}

.footer .box-container .box a:hover
{
    color: var(--orange);
    text-decoration: none;
}

.footer .copyright
{
    text-align: center;
    border: .1rem solid rgba(0,0,0,.1);
    font-size: 1.5rem;
    color: var(--black);
    padding: .5rem;
    padding-top: .5rem;
    margin-top: 2.5rem;
    margin-bottom: .5rem;
}

.footer .copyright a
{
    color: var(--orange);
}
.tecktec {
    font-size: 20px;
}
.teckte:hover {
    color:#ff7b00;;
}
.Iranfood {
    color:#ff7b00;
    cursor: pointer;
    font-size: 20px;
    font-weight: bold;
}
.title-logo {
    margin-top: 20px;
    color: red;
    font-family: 'Courier New', Courier, monospace;
}
     /* medida queries */

     @media (max-width:991px)
     {
         html
         {
             font-size: 55%;
         }
     
         header
         {
             padding:1rem 2rem ;
         }
         section
         {
             padding:2rem ;
         }
     }
     
     @media (max-width:768px)
     {
         header .icons #menu-bars
     {
         display: inline-block;
     }
         header .navbar
         {
             position: absolute;
             top: 100%;
             left: 0;
             right: 0;
             background: #fff;
             border-top: .1rem solid rgba(0,0,0,.2);
             border-bottom: .1rem solid rgba(0,0,0,.2);
             padding: 1rem ;
             clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
         }
         header .navbar.active
         {
             clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
         }
     
         header .navbar a
         {
             display: block;
             padding: 1.5rem;
             margin: 1rem;
             font-size: 2rem;
             background: #eee;
     
         }
     
         #search-form #search-box
         {
             width: 90%;
             margin: 0 1rem;
         }
     
         .home .home-slider .slide .content h3
         {
         font-size: 5rem;
         }
     }
     
     @media (max-width:450px)
     {
         html
         {
             font-size: 50%;
         }
     
         .dishes .box-container .box img
         {
         height: auto;
         width: 100%;
         }
         .order form .inputbox .input
         {
         width: 100%;
         }
     
         .home .home-slider .slide .content h3
         {
         font-size: 5rem;
         }
     
     }
     
     @media (max-width:375px)
     {
     html
         {
             font-size: 50%;
         }
      .home .home-slider .slide .content h3
         {
         font-size: 5rem;
         }
     .home .home-slider .slide {
       padding-top: 8rem;
       padding-bottom: 3rem;
     }
     }
     
     .home .home-slider .slide
     {
         display: flex;
         align-items: center;
         flex-wrap: wrap;
         gap: 2rem;
         padding-top: 9rem;
     }
     
     .home .home-slider .slide .content
     {
         flex: 1 1 40rem;
     
     }
     
     .home .home-slider .slide .image
     {
         flex: 1 1 40rem;
         
     }
     
     .home .home-slider .slide .image img
     {
         width: 100%;
     }
     
     .home .home-slider .slide .content span
     {
         color: var(--orange);
         font-size: 2.5rem;
     }
     
     .home .home-slider .slide .content h3
     {
         color: var(--black);
         font-size: 6rem;
     }
     
     .home .home-slider .slide .content p
     {
         color: var(--light-color);
         font-size: 2.2rem;
         padding: .5rem 0;
         line-height: 1.5;
     }