@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;500;600&display=swap');

:root
{
    --orange:#ff7b00;
    --black:#192a56;
    --light-color:#666;
    --box-shadow:0 .5rem 1.5rem rgba(0,0,0,.1);
}

*
{
    font-family: 'Nunito', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    outline: none;
    border: none;   
    text-transform: capitalize;
    transition: all .2s linear;
}

html
{
    font-size: 62.5%;
    overflow-x: hidden;
    scroll-padding-top: 5.5rem;
    scroll-behavior: smooth;
}

section
{
    padding: 2rem 9%;
}

section:nth-child(even)
{
    background: #eee;
}

.sub-deading
{
    text-align: center;
    color: var(--orange);
    font-size: 2rem;
    padding-top: 1rem;
}

.heading
{
    text-align: center;
    color: var(--black);
    font-size: 3rem;
    padding-bottom: 2rem;
    text-transform: uppercase;
}

.btn
{
    margin-top: 1rem;
    display: inline-block;
    font-size: 1.7rem;
    color: #fff;
    background: var(--black);
    border-radius: .5rem;
    cursor: pointer;
    padding: .8rem 2rem;
}

.btn:hover
{
    background: var(--orange);
    letter-spacing: .1rem;
}


.swiper-pagination-bullet-active
{
    background: var(--orange);
}

.dishes .box-container
{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(28rem, 1fr));
    gap:2.5rem;

}

.dishes .box-container .box
{
    padding: 2.5rem;
    background: #fff;
    border-radius: .5rem;
    border: .1rem solid rgba(0,0,0,.2);
    box-shadow: var(--box-shadow);
    position: relative;
    overflow: hidden;
    text-align: center;
}

.dishes .box-container .box .fa-heart,
.dishes .box-container .box .fa-eye
{
    position: absolute;
    top: 1.5rem;
    background: #eee;
    border-radius: 50%;
    height: 5rem;
    width: 5rem;
    line-height: 5rem;
    font-size: 2rem;
    color: var(--black);
}

.dishes .box-container .box .fa-heart:hover,
.dishes .box-container .box .fa-eye:hover
{
    background: var(--orange);
    color: #fff;
}

.dishes .box-container .box .fa-heart
{
    right:-15rem;
}

.dishes .box-container .box .fa-eye
{
    left:-15rem;
}

.dishes .box-container .box:hover .fa-heart
{
    right:1.5rem;
}

.dishes .box-container .box:hover .fa-eye
{
    left:1.5rem;
}

.dishes .box-container .box img
{
    height: 25rem;
    margin: 1rem 0;
}

.dishes .box-container .box h3
{
    color: var(--black);
    font-size: 2.5rem;
}

.dishes .box-container .box .starts
{
    padding: 1rem 0;
}

.dishes .box-container .box .starts i
{
    font-size: 1.7rem;
    color: var(--orange);
}

.dishes .box-container .box span
{
    color: var(--orange);
    font-weight: bolder;
    margin-right: 1rem;
    font-size: 2.5rem;
}

.about .row
{
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    align-items: center;

}

.about .row .image
{
    flex: 1 1 45rem;
}

.about .row .image
{
    flex: 1 1 45rem;
}

.about .row .image img
{
    width: 100%;
}

.about .row .content
{
    flex: 1 1 45rem;
}

.about .row .content h3
{
    color: var(--black);
    font-size: 4rem;
    padding: .5rem 0;

}

.about .row .content p
{
    color: var(--light-color);
    font-size: 1.5rem;
    padding: .5rem 0;
    line-height: 2rem;
}

.about .row .content .icons-container
{
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    padding: 1rem 0;
    margin-top: .5rem;
}

.about .row .content .icons-container .icons
{
    background: #eee;
    border-radius: .5rem;
    border: .1rem solid rgba(0,0,0,.2);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    flex:  1 1 17rem;
    padding: 1.5rem 1rem;
}

.about .row .content .icons-container .icons i
{
    font-size: 2.5rem;
    color: var(--orange);
}

.about .row .content .icons-container .icons i span
{
    font-size: 1.5rem;
    color: var(--black);
}

.menu .box-container
{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
    gap:2.5rem;

}

.menu .box-container .box
{
    background: #fff;
    border: 1rem solid rgba(187, 187, 187,.2);
    border-radius: .5rem;
    box-shadow: var(--box-shadow);

}

.menu .box-container .box .image
{
    height: 26rem;
    width: 100%;
    padding: 1.5rem;
    overflow: hidden;
    position: relative;
}



/* medida queries */

@media (max-width:991px)
{
    html
    {
        font-size: 55%;
    }

    header
    {
        padding:1rem 2rem ;
    }
    section
    {
        padding:2rem ;
    }
}

@media (max-width:768px)
{
    header .icons #menu-bars
{
    display: inline-block;
}
    header .navbar
    {
        position: absolute;
        top: 100%;
        left: 0;
        right: 0;
        background: #fff;
        border-top: .1rem solid rgba(0,0,0,.2);
        border-bottom: .1rem solid rgba(0,0,0,.2);
        padding: 1rem ;
        clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
    }
    header .navbar.active
    {
        clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
    }

    header .navbar a
    {
        display: block;
        padding: 1.5rem;
        margin: 1rem;
        font-size: 2rem;
        background: #eee;

    }

    #search-form #search-box
    {
        width: 90%;
        margin: 0 1rem;
    }

    .home .home-slider .slide .content h3
    {
    font-size: 5rem;
    }
}

@media (max-width:450px)
{
    html
    {
        font-size: 50%;
    }

    .dishes .box-container .box img
    {
    height: auto;
    width: 100%;
    }
    .order form .inputbox .input
    {
    width: 100%;
    }

    .home .home-slider .slide .content h3
    {
    font-size: 5rem;
    }

}

@media (max-width:375px)
{
html
    {
        font-size: 50%;
    }
 .home .home-slider .slide .content h3
    {
    font-size: 5rem;
    }
.home .home-slider .slide {
  padding-top: 8rem;
  padding-bottom: 3rem;
}
}

.home .home-slider .slide
{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 2rem;
    padding-top: 9rem;
}

.home .home-slider .slide .content
{
    flex: 1 1 40rem;

}

.home .home-slider .slide .image
{
    flex: 1 1 40rem;
    
}

.home .home-slider .slide .image img
{
    width: 100%;
}

.home .home-slider .slide .content span
{
    color: var(--orange);
    font-size: 2.5rem;
}

.home .home-slider .slide .content h3
{
    color: var(--black);
    font-size: 6rem;
}

.home .home-slider .slide .content p
{
    color: var(--light-color);
    font-size: 2.2rem;
    padding: .5rem 0;
    line-height: 1.5;
}